.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

html{
  background: url(./assets/cork-board-dark.jpg);
  background-size: contain;
  background-repeat: repeat;
  font-size: 62.5%;
}

.wrapper{
  max-width: 1200px;
  margin-top: 50px;
}

.notes{
  width: 53%;
  margin-left: 2%;
}

.photos{
  width: 45%;
}

h1{
  text-shadow: 2px 2px 4px #ffffff;
}

dialog{
  position: absolute;
  top: 30vh;
  width: 300px;
  padding: 0;
  z-index: 10;
}

form{
  width: 100%;
  display: flex;
  flex-direction: column;
}

textarea {
  background: rgb(250, 237, 166);
  resize: none;
  border: none;
  position: relative;
  bottom: 1px;
  font-family: 'calibri', sans-serif;
  padding: 13.5px 15px;
  font-size: 1.75rem;
  height: 200px;
}

form button{
border: none;
background: rgb(248, 228, 137);
}

.draggable{
  background: rgb(248, 228, 137);
  display: flex;
  justify-content: flex-end;
}

.draggable span,
form button{
  padding: 5px 10px;
  font-family: cursive;
  font-weight: bold;
  display: block;
  cursor: pointer;
  color: #282c34a4;
  width: 30px;
  font-size: 1.5rem;
}

.draggable span:hover,
.draggable span:focus,
form button:hover,
form button:focus{
  background: rgb(230, 207, 109);
}

form .draggable{
  justify-content: space-between;
}

form button[type=submit]{
  font-family: "Calibri", sans-serif;
  width: 110px;
  align-self: flex-end;
  font-size: 1.75rem;
}

ul{
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  justify-content: flex-end;
  list-style: none;
  padding: 0;
}

li{
  padding: 1%;
}

img{
  max-height: 200px;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
}

main{
  display: flex;
  min-height: 100vh;
  max-height: 100%;
}

button[type=open],
.custom-file-upload{
  font-weight: bolder;
  background:white;
  cursor: pointer;
  color: #282c34a4;
  border: none;
  border-radius: 35px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.603);
  transition: 0.3s;
}

button[type=open]{
  padding: 6px 14px;
  font-size: 3.5rem;
  padding-top: 0;
  position: absolute;
  right: 15px;
  top: 200px;
}

button[type=open]:hover,
button[type=open]:focus,
.custom-file-upload:hover,
.custom-file-upload:focus{
  color: #2196F3;
}

.visually-hidden { 
  position: absolute !important;
  height: 1px; 
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; /* added line */
}

.custom-file-upload{
  font-size: 2.75rem;
  cursor: pointer;
  padding: 7px 13px;
  position: absolute;
  top: 50px;
  right: 15px;
}
/* The switch - the box around the slider */
.switch {
  position: absolute;
  display: inline-block;
  top:10px;
  right: 10px;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2196F3;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #ccc;
  box-shadow: 0 0 3px rgb(170, 170, 170);
}

input + .slider {
  box-shadow: 0 0 3px rgb(29, 125, 204);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Round sliders */
.slider {
  border-radius: 34px;
}

.slider:before {
  border-radius: 50%;
}

input[type=file]{
  padding: 15px;
}